import React from "react";
import { CustomColumns, Page } from "../../types";
import { useQuery } from "react-query3";
import { Modal } from "components/ui/Modal";
import { Button, PageHeader, Space, Tag, Tooltip } from "antd";
import Table from "../ui/Table";
import EditModal from "components/ui/EditModal";
import DeleteButton from "components/ui/DeleteButton";
import { EyeOutlined } from "@ant-design/icons";
import DocsButton from "components/docsButton";
import { toRelativeUrl } from "components/utils/utils";
const PageModal = React.lazy(() => import("./PageModal"));

export default function Pages() {
  const { data: pages, isLoading, refetch } = useQuery<Page[]>(
    "/page",
    { enabled: false, refetchOnWindowFocus: false }
  );

  const columns: CustomColumns<Page>[] = [
    {
      key: "name",
      title: "Name",
      dataIndex: "name",
      editable: true,
    },
    {
      key: "url",
      title: "URL",
      dataIndex: "url",
      editable: true,
      render: (text, record) => {
        return text && text !== '' ? text : record.name
      }
    },
    {
      title: "Description",
      key: "description",
      dataIndex: "description",
      editable: true,
    },
    {
      key: "authenticated",
      title: "Authentication",
      dataIndex: "authenticated",
      align: "center",
      render: (text, record) =>
        record?.authenticated ? "Enabled" : "Disabled",
    },
    {
      key: "role",
      title: "Role",
      dataIndex: "role",
      render: (value, record) => {
        return record.roles?.length > 0
          ? record?.roles?.map((role) => (
            <Tag key={role} color="blue">
              {role}
            </Tag>
          ))
          : null;
      },
    },
    {
      width: "fit-content",
      dataIndex: "actions",
      editable: false,
      render: (text, record) => {
        let url = record?.url && record?.url !== '' ? record?.url : record.name;
        if (url.startsWith("/")) {
          url = url.substring(1, url.length);
        }

        return (
          <Space style={{ justifyContent: "flex-end", width: "100%" }}>
            <Modal>
              <EditModal record={record} allowedWithOneWayGitSync={false} />
            </Modal>
            <DeleteButton resource={record} allowedWithOneWayGitSync={false} />
            <Tooltip title="View">
              <Button type="text" icon={<EyeOutlined />} href={toRelativeUrl(`/${url}`)} target="_blank" />
            </Tooltip>
          </Space>
        );
      },
    },
  ];

  return (
    <PageHeader title="Pages" subTitle="Simple, drag and drop web pages that can execute PowerShell and REST APIs." extra={[
      <DocsButton url="userinterfaces/pages" />
    ]}>
      <Table
        title="Pages"
        actions={[
          <Modal>
            <PageModal />
          </Modal>,
        ]}
        columns={columns}
        data={pages}
        loading={isLoading}
        queryFn={refetch}
      />
    </PageHeader>

  );
}
